.App {
  text-align: center;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
  background: white;
  padding: 1rem;
  border-radius: 5px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

button {
  margin-top: 2rem;
}

button#start, button#stop {
  color: white;
  padding: .5rem;
  border-radius: 5px;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  font-weight: bolder;
  font-size: 1.25rem;
  border: none;
  transition: .2s;
  cursor: pointer;
}

button#start:hover, button#stop:hover {
  scale: 1.03;
}

button#start { background: dodgerblue; }
button#start:hover { background: skyblue; }
button#stop { background: red; }
button#stop:hover { background: crimson; }

button#started {
  height: 3rem;
  width: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 3rem;
  background: white;
  border: none;
  border-radius: 5px;
  color: black;
  font-weight: bolder;
  font-size: 1.25rem;
  cursor: pointer;
}

button#started:hover {
  background: lightgray;
}

h2 {
  color: white;
  font-weight: bolder;
}

code {
  color: black;
  background: lightgray;
  border-radius: 4px;
  padding-left: .3rem;
  padding-right: .3rem;
}

select {
  height: 2rem;
  width: auto;
  border-radius: 4px;
  text-align: center;
  font-size: 1rem;
  padding-right: .3rem;
}